'use client';
import React from 'react'
import { useJob } from '../context/context';
import { useRouter } from 'next/router';
type Posting = {
  id:number;
  title: string;
  description: string;
  responsibilities: string[];
  ideal_candidate: {
    age: string;
    gender: string;
    traits: string[];
  };
  when_where: string;
  about: {
    posted_on: Date;
    deadline: Date;
    location: string;
    start_date: string;
    end_date: string;
    categories: string[];
    required_skills: string[];
  };
  company: string;
  image: string;
};

function Show() {
    const {selectedJob}=useJob()
    const router=useRouter()
    if(!selectedJob){
      router.push('/')
    }
  return (
    <div>
      {selectedJob?.company}
    </div>
  )
}

export default Show
